*,
*::before,
*::after {
  box-sizing: border-box;
}

ul[class],
ol[class] {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

ul[class],
ol[class] {
  list-style: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

main.main {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
}

.shopping-cart, .search, .phone, .menu, .envelope, .ch-top, .ch-right, .ch-left, .ch-bottom {
  background: url("sprite.svg") no-repeat;
}

.ch-bottom {
  background-position: 0 0;
}

.ch-bottom-dims {
  width: 50px;
  height: 50px;
}

.ch-left {
  background-position: 50% 0;
}

.ch-left-dims {
  width: 50px;
  height: 50px;
}

.ch-right {
  background-position: 0 50%;
}

.ch-right-dims {
  width: 50px;
  height: 50px;
}

.ch-top {
  background-position: 50% 50%;
}

.ch-top-dims {
  width: 50px;
  height: 50px;
}

.envelope {
  background-position: 100% 0;
}

.envelope-dims {
  width: 50px;
  height: 50px;
}

.menu {
  background-position: 100% 50%;
}

.menu-dims {
  width: 50px;
  height: 50px;
}

.phone {
  background-position: 0 100%;
}

.phone-dims {
  width: 50px;
  height: 50px;
}

.search {
  background-position: 50% 100%;
}

.search-dims {
  width: 50px;
  height: 50px;
}

.shopping-cart {
  background-position: 100% 100%;
}

.shopping-cart-dims {
  width: 50px;
  height: 50px;
}

header.header {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  background: #d18b0a;
  height: 240px;
  max-width: 1920px;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  font-style: italic;
}

.header__nav {
  position: absolute;
  top: 3%;
  right: 6%;
}

.header__nav_menu {
  position: relative;
  top: 0;
  left: 0;
  width: 60px;
  height: 60px;
  background: #212121;
  padding: 15px;
  border-radius: 4px;
}

.menu {
  width: 30px;
  height: 30px;
  cursor: pointer;
  opacity: 0.4;
  transition: all 0.5s linear;
}
.menu:hover {
  opacity: 1;
}

section.content {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 180px;
  background: #212121;
  color: #ffffff;
  text-align: right;
  padding-right: 12%;
}

footer.footer {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 260px;
  background: #c4c4c4;
  text-align: right;
  padding-right: 12%;
}

@font-face {
  font-family: "Alice Regular";
  src: url("../fonts/Alice Regular.woff2") format("woff2"), url("../Alice Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: "Verdana Bold";
  src: url("../fonts/Verdana Bold.woff2") format("woff2"), url("../Verdana Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: "Verdana Regular";
  src: url("../fonts/Verdana Regular.woff2") format("woff2"), url("../Verdana Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
  font-weight: normal;
}